import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import background from '../assets/background.jpg'
import logo from '../logo.png'
import image1 from '../assets/image1.png'
import image2 from '../assets/image2.png'
import BasicCard from './Card'
import '../styles.css'
import "../css/fonts.css"
import WeekendIcon from '@mui/icons-material/Weekend';
import KeyIcon from '@mui/icons-material/Key';
import NightlifeIcon from '@mui/icons-material/Nightlife';
import pinkgirl from '../assets/pinkgirl.png'
import whitebikini from '../assets/whitebikini.png'
import girlbra from '../assets/girlbra.png'
import Flickity from 'react-flickity-component'
import carousel1 from '../assets/carousel1.jpg'
import carousel2 from '../assets/carousel2.jpg'
import carousel3 from '../assets/carousel3.jpg'
import lounge1 from '../assets/lounge1.jpg'
import lounge2 from '../assets/lounge2.jpg'

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#b6495b' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const flickityOptions = {
  prevNextButtons: false,
  pageDots: false,
  initialIndex: 1,
  contain: true
}

export default function FourthSection() {
  return (
    <Box sx={{ flexGrow: 1, fontFamily: 'GothamRegular' }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
            <Item sx={{ marginTop: '4%', backgroundImage: 'unset', boxShadow: 'unset', backgroundColor: 'unset', fontFamily: 'GTA' }}>
              <h1 className="S2-title">Gallery</h1>
            </Item>
        </Grid>
      </Grid>
      <Flickity
          className={'carousel'} // default ''
          elementType={'div'} // default 'div'
          options={flickityOptions} // takes flickity options {}
          disableImagesLoaded={false} // default false
          reloadOnUpdate // default false
          static // default false
        >
          <img className="carousel-image" src={lounge1}/>
          <img className="carousel-image" src={lounge2}/>
          <img className="carousel-image" src={carousel1}/>
          <img className="carousel-image" src={carousel2}/>
          <img className="carousel-image" src={carousel3}/>
          <img className="carousel-image" src={carousel1}/>
        </Flickity>
    </Box>
  );
}