import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import background from '../assets/background.jpg'
import logo from '../logo.png'
import image1 from '../assets/image1.png'
import image2 from '../assets/image2.png'
import '../styles.css'

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

export default function FirstSection() {
  return (
    <Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
            <Box
                sx={{
                    height: 600,
                    backgroundColor: 'primary.dark',
                    borderRadius: 10,
                    backgroundImage: `url(${background})`,
                    backgroundPosition: 'center',
                    backgroundSize: 'cover',
                    marginBottom: 5
                }}
            >
                <Grid container sx={{height: '100%', p:2}}>
                    <Grid item xs={12}>
                        <Box className='Div-text'
                            sx={{
                                height: '100%',
                                // backgroundColor: 'rgba(241,149,172,0.3)',
                                borderRadius: 10,
                            }}
                        >
                          <img src={image1} className="Div-image" alt="image1"/>
                          <div className="Div-middle-title font-link">
                            <h1 className="gta text-50">Welcome to<br />BKK vice</h1>
                          </div>
                          <img src={image2} className="Div-image" alt="image1"/>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </Grid>
      </Grid>
    </Box>
  );
}