import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import background from '../assets/background.jpg'
import logo from '../logo.png'
import image1 from '../assets/image1.png'
import image2 from '../assets/image2.png'
import BasicCard from './Card'
import '../styles.css'
import "../css/fonts.css"
import WeekendIcon from '@mui/icons-material/Weekend';
import KeyIcon from '@mui/icons-material/Key';
import NightlifeIcon from '@mui/icons-material/Nightlife';
import pinkgirl from '../assets/pinkgirl.png'
import whitebikini from '../assets/whitebikini.png'
import girlbra from '../assets/girlbra.png'

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#b6495b' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

export default function ThirdSection() {
  return (
    <Box sx={{ flexGrow: 1, fontFamily: 'GothamRegular' }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
            <Item sx={{ backgroundImage: 'unset', boxShadow: 'unset', backgroundColor: 'unset', fontFamily: 'GTA' }}>
              <h1 className="S2-title">Our services</h1>
            </Item>
        </Grid>
        <Grid item xs={4} className="Container-girl">
            <Item className="Img-girl" sx={{ paddingInline: '5%', backgroundImage: 'unset', boxShadow: 'unset', backgroundColor: 'unset' }}>
              <img src={whitebikini} alt="whitebikini"/>
            </Item>
        </Grid>
        <Grid item xs={4} className="Container-girl">
            <Item className="Img-girl" sx={{ paddingInline: '5%', backgroundImage: 'unset', boxShadow: 'unset', backgroundColor: 'unset' }}>
              <img src={pinkgirl} alt="pinkgirl"/>
            </Item>
        </Grid>
        <Grid item xs={4} className="Container-girl">
            <Item className="Img-girl" sx={{ paddingInline: '5%', backgroundImage: 'unset', boxShadow: 'unset', backgroundColor: 'unset' }}>
              <img src={girlbra} alt="girlbra"/>
            </Item>
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={4}>
            <Item sx={{height: '100%'}}>
              <BasicCard title="Fairy Lounge" text1={NightlifeIcon} text2="Unique interior design" body="A full 360° ecstatic experience right in the heart of Sukhumvit"/>
            </Item>
        </Grid>
        <Grid item xs={4}>
            <Item sx={{height: '100%'}}>
              <BasicCard title="Unique Experience" text1={KeyIcon} text2="Affordable Luxury" body="Explore your sensual side in a safe and discreet environment"/>
            </Item>
        </Grid>
        <Grid item xs={4}>
            <Item sx={{height: '100%'}}>
            <BasicCard title="Rooms & Suites" text1={WeekendIcon} text2="Expect Only The Best" body="All our rooms are designed to the highest standards All our rooms are designed to the highest standards"/>
            </Item>
        </Grid>
      </Grid>
    </Box>
  );
}